* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



.container {
    width: 100%;
    // height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    justify-content: center;
    overflow: scroll;

    .socialImage {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 0.5px solid black;
        padding: 0px 5px;

        .leftSide{
            img{
                width: 30px;
                height: 30px;
            }
        }

        .edit{
            display: flex;
            align-items: center;
            gap: 10px;

            p{
                cursor: pointer;
            }

            p:hover{
                color: red;
            }
        }
        
       
    }

    .addContainer {
        display: none;
        background-color: red;
        width: 500px;
        height: 500px;
        position: absolute;
        top: 50%;
        /* Move container down by 50% of parent height */
        left: 50%;
        /* Move container right by 50% of parent width */
        transform: translate(-50%, -50%);
        justify-content: center;
        padding: 20px 15px;
        border-radius: 20px;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        overflow: auto;
        justify-content: flex-start;

        .inputs {
            display: flex;
            flex-direction: column;
            gap: 5px;
            align-items: center;

            .inputContainer {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 300px;
                height: 40px;
                background-color: white;
                border-radius: 10px;
                padding: 10px 15px;

                input {
                    width: 100%;
                    height: 100%;
                    border: 0;
                    outline: 0;
                    // border-radius: 10px;

                }
            }

            .textareaContainer {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 300px;
                height: 200px;
                background-color: white;
                border-radius: 10px;
                padding: 10px 15px;

                textarea {
                    width: 100%;
                    height: 100%;
                    outline: 0;
                    border: 0;
                    resize: none;
                }
            }

            .imagePreview {

                .previewContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                }

            }

            img {
                width: 100px;
                height: 100%;
            }

        }

        .buttons {
            width: 100%;
            height: auto;
            display: flex;
            gap: 20px;
            // align-items: flex-end;
            justify-content: center;
            background-color: transparent;

            button {
                padding: 10px 30px;
                border-radius: 20px;
                border: 0;
                outline: 0;
                cursor: pointer;
            }

            .cancelBtn {
                background-color: white;
                border: 1px solid rgb(242, 242, 242);
                color: black;
            }

            .updateBtn {
                background-color: black;
                color: white;
            }

        }


    }
}